.page {
  background-color: #000000;
  color: #FFFFFF;
  max-width: 100%;
  min-width: 320px;
  min-height: 100vh;
  margin: 0 auto;
  font-family: 'Inter', Arial, sans-serif;
  -moz-osx-font-smoothing: grayscale;
  -webkit-font-smoothing: antialiased;
  -webkit-text-size-adjust: 100%;
  -ms-text-size-adjust: 100%;
  -moz-text-size-adjust: 100%;
  padding: 0;
}

.header {
  max-width: 880px;
  min-height: 74px;
  padding-top: 45px;
  margin: 0 auto;
  border-bottom: 1px solid rgba(84, 84, 84, 0.7);
  display: flex;
  justify-content: space-between;
}

@media screen and (max-width: 999px) {
  .header {
    max-width: 85%;
  }
}

@media screen and (max-width: 767px) {
  .header {
    max-width: 320px;
    min-height: 56px;
    padding-top: 0;
  }
}

@media screen and (max-width: 767px) {
  .header_isloggedIn {
    display: grid;
    grid-auto-flow: row;
  }
}

.header__menu {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding-top: 28px;
}

@media screen and (max-width: 767px) {
  .header__menu {
    width: 320px;
    padding-top: 0;
  }
}

.header__menu-button {
  display: grid;
  grid-auto-flow: row;
  margin-right: 30px;
  list-style-type: none;
}

.header__menu-button:hover {
  opacity: 0.6;
  transition: opacity 0.3s;
  cursor: pointer;
}

.header__menu-button-null {
  list-style-type: none;
  padding: 0;
  margin: 0;
}

.header__logo {
  width: 142px;
  height: 33px;
  object-fit: contain;
  padding-bottom: 41px;
}

@media screen and (max-width: 767px) {
  .header__logo {
    width: 103.74px;
    height: 24.4px;
    margin-left: 30px;
    margin-top: 27px;
    margin-bottom: 31.6px;
    padding-bottom: 0;
  }
}

.header__link {
  text-decoration: none;
  transition: 0.3s;
  color: #FFF;
  font-size: 18px;
  line-height: 1.22;
  cursor: pointer;
}

.header__link:hover {
  opacity: 0.6;
}


@media screen and (max-width: 767px) {
  .header__link {
    font-size: 14px;
    line-height: 1.21;
    margin-bottom: 40px;
    margin-right: 30px;
  }
}

@media screen and (max-width: 767px) {
  .header__link_isloggedIn {
    margin-right: 0;
  }
}

.header__email {
  color: #FFF;
  font-weight: 500;
  font-size: 18px;
  line-height: 1.22;
  margin-right: 24px;
  margin-bottom: 0;
}

@media screen and (max-width: 767px) {
  .header__email {
    font-size: 14px;
    line-height: 1.21;
    margin-bottom: 40px;
  }
}

.header__email_isloggedIn {
  margin-right: 24px;
  margin-top: 0;
}

@media screen and (max-width: 767px) {
  .header__email_isloggedIn {
    margin-right: 0;
    margin-bottom: 18px;
    margin-top: 12px;
  }
}

.header__nav {
  display: flex;
  flex-direction: row;
  margin-bottom: 40px;
}

.header__nav_isloggedIn {
  display: flex;
  flex-direction: row;
  align-items: center;
}

@media screen and (max-width: 767px) {
  .header__nav_isloggedIn {
    display: grid;
    order: -1;
    border-bottom: 1px solid rgba(84, 84, 84, 0.7);
    width: 320px;
    justify-items: center;
    padding-top: 40px;
  }
}

@media screen and (max-width: 767px) {
  .header__nav_isMenuClose {
    height: 0;
    width: 0;
    z-index: -1;
    padding-top: 0;
  }
}

@media screen and (max-width: 767px) {
  .header__button_action_close {
    width: 20px;
    height: 20px;
    background-image: url(/react-mesto-auth/static/media/Close-Icon.cb0f2bbf.svg);
    background-color: rgba(0, 0, 0, 0);
    background-repeat: no-repeat;
    background-position: center;
    background-size: cover;
    margin-right: 30px;
  }
}

@media screen and (max-width: 767px) {
.header__button_action_open {
  width: 24px;
  height: 6px;
  background-color: rgba(0,0,0,0);
  border-bottom: 3px solid #FFFFFF;
  border-top: 3px solid #FFFFFF;
  outline: none;
  background: none;
  list-style-type: none;
  margin-right: 30px;
}

.header__button_action_open:last-of-type {
  border-top: none;
}
}

