.popup {
  display: flex;
  position: fixed;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  background: rgba(0, 0, 0, 0.5);
  justify-content: center;
  align-items: center;
  visibility: hidden;
  opacity: 0;
  transition: visibility 0.5s, opacity 0.5s linear;
  z-index: 2;
}



.popup_opened {
  visibility: visible;
  opacity: 1;
  transition: visibility 0.5s, opacity 0.5s linear;
}

.popup_image {
  background-color: rgba(0, 0, 0, 0.9);
  z-index: 2;
}

.popup__container {
  position: relative;
  min-height: 181px;
  max-width: 430px;
  box-sizing: border-box;
  color: #000000;
  background: #FFFFFF;
  box-shadow: 0px 0px 25px rgba(0, 0, 0, 0.15);
  border-radius: 10px;
}


@media screen and (max-width: 767px) {
  .popup__container {
    max-width: 282px;
  }
}


.popup__container-close-button {
  width: 32px;
  height: 32px;
  background-image: url(/react-mesto-auth/static/media/Close-Icon.cb0f2bbf.svg);
  background-color: rgba(0,0,0,0);
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;
  position: absolute;
  top: -40px;
  right: -40px;
  border: none;
  padding: 0;
